.impression{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 17px;
}

.impression__slogan{
  margin-bottom: 36px;
  font-size: 18px;
  font-weight: bold;
  line-height: 26px;
  text-align: center;
}

.impression__list{
  list-style: none;
  margin: 0;
  padding: 0;
}

.impression__item{
  position: relative;
  min-height: 50px;
  margin-bottom: 20px;

}

.impression__field{

  &:checked,
  &:not(:checked){
    position: absolute;
    left: -9999px;
  }
}

.impression__field:checked + .impression__heading::before,
.impression__field:not(:checked) + .impression__heading::before{
  content: "";
  position: absolute;
  left: -30px;
  bottom: 13px;
  width: 46px;
  height: 46px;
  border-radius:100%;
  background-color: #f2f2f2;
}

.impression__field:checked + .impression__heading::after,
.impression__field:not(:checked) + .impression__heading::after{
  content: "";
  position: absolute;
  left: -15px;
  bottom: 28px;
  width: 16px;
  height: 16px;
  border-radius:100%;
  background-color: #333333;
}

.impression__field:not(:checked) + .impression__heading::after{
  opacity: 0;
}

.impression__field:checked + .impression__heading::after{
  opacity: 1;
}

.impression__heading{
  padding-left: 40px;
  font-size: 18px;
  font-weight: bold;
  line-height: 26px;
  text-transform: none;
  text-align: left;
}

@media(min-width: @tablet-width){

  .impression{
    align-items: flex-start;
    width: 324px;
    order: -1;
  }

  .impression__list{
    padding: 37px 25px 11px;
  }

  .impression__item{
    margin-bottom: 28px;
  }

  .impression__slogan{
    margin-top: 30px;
    font-size: 21px;
  }
}

@media(min-width: @desktop-width){
  .impression{
    width: 480px;
  }

  .impression__slogan{
    margin-left: 80px;
  }
}
